body, html, .App {
  margin: 0;
  padding: 0;
  height: 100vh;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

body {
  background-color: lightgray;
  font-family: 'Roboto', sans-serif;
}

.paper {
  text-align: center;
  overflow: auto !important;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 0 24px 24px 24px;
}

.margin-bottom {
  margin-bottom: 10px;
}

.heading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 8px 0;
}

.typography {
  display: -ms-flexbox  !important;
  display: flex  !important;
  -ms-flex-pack: center !important;
      justify-content: center !important;
}

.headingTypography {
  display: -ms-flexbox;
  display: flex;
  line-height: 1.5em !important;
  padding: 0 10px !important;
}

.result {
  padding: 8px 24px;
  background-color: aquamarine;
}

.paddingTop0 {
  padding-top: 0;
}

.icon {
  height: 64px;
  width: 64px;
}

.mobileTooltip {
  margin: 10px 0 !important;
  font-size: 0.85rem !important;
}


@media only screen and (max-width: 600px) {
  .paper {
    height: 100vh;
    padding-top: 24px;
  }

  .icon {
    padding: 10px 0;
    width: 85px;
    height: 85px;
  }

  .headingTypography {
    -ms-flex: 1 1;
        flex: 1 1;
  }

  .mobileTooltip {
    font-size: 0.75rem;
  }
}


